@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

.main-header {
}

/* #239981 
#994323  */

.hero-container {
  height: 100vh;
  background-image: linear-gradient(
      to right bottom,
      rgba(35, 153, 129, 0.65),
      rgba(199, 249, 204, 0.65)
    ),
    url(/Initial-page/static/media/human.31faf758.png);
  background-size: cover;
  background-position: top;
  -webkit-clip-path: polygon(74% 100%, 100% 82%, 100% 0, 26% 0, 0 18%, 0 100%);
          clip-path: polygon(74% 100%, 100% 82%, 100% 0, 26% 0, 0 18%, 0 100%);
  position: relative;
}

.symbol-svg {
  width: 12rem;
  fill: #239981;
  stroke: #239981;
  position: absolute;
  top: 40px;
  left: 40px;
  opacity: 1;
  transition: 0.4s;
}

.symbol-svg:hover {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.main-header {
  font-family: "Special Elite", cursive;
  position: absolute;
  top: 65%;
  left: 10%;
  color: #fff;
  letter-spacing: 3px;
  font-size: 9.6rem;
  -webkit-animation-name: moveInLeft;
          animation-name: moveInLeft;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  text-align: center;
}

.subtitle {
  margin-bottom: 1.2rem;
  font-size: 3rem;
  display: block;
}

.btn {
  font-family: "Special Elite", cursive;
  font-size: 3.2rem;
  color: #239981;
  background-color: white;
  text-transform: uppercase;
  text-decoration: none;
  padding: 1.5rem 4rem 1rem 4rem;
  border-radius: 2rem;
  border-style: hidden;
  display: inline-block;
  transition: all 0.2s;
  -webkit-animation-name: moveInDown;
          animation-name: moveInDown;
  cursor: pointer;
}

.btn:hover {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  box-shadow: 0 10px 20px;
}

.btn:active {
  -webkit-transform: translateY(-1px);
          transform: translateY(-1px);
  box-shadow: 0 10px 10px;
}

@-webkit-keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@-webkit-keyframes moveInDown {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes moveInDown {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

